import { Link } from "gatsby";
import React from "react";
import "./header.scss";
const menu = require("./../menu.json");
const Header = () => (
  <header
    style={{
      background: `rgba(68,218,179,1)`,
    }}
  >
    <div
      className="header-wrap"
    >
      <div
        className="logo"
        style={{
          margin: `0 1rem`,
          color: `white`,
          fontSize: `4rem`,
          fontFamily: `Fantasy`
        }}
      >
        z7xisoo
      </div>

      <div
        style={{
          display: `flex`,
          paddingRight: `1rem`
        }}
      >
        {
          menu.map((item, key) => (
            <Link
              key={key}
              to={item.path}
              style={{
                color: `white`,
                textDecoration: `none`,
                alignItems: `center`,
                margin: `0 .3rem`,
                display: `flex`,
                fontWeight: `bolder`,
                fontSize: `1.5rem`,
                fontFamily: `Fantasy`
              }}
              title={item.content}
            >
              {item.title}
            </Link>
          ))
        }
      </div>
    </div>
  </header>
)
export default Header
